//비동기로 DB 데이터를 받을 때, 페이지 전환시 clear 처리 
//AbortController : 각 사용자의 브라우저 내에서 로컬로 관리
const abortController = () => {
  const controllers = {};

  return {
    //20240529 sw : scope 매개변수 (crtller의 키값)  
    //20240529 sw : signal 관리 
    add(scope) {
      if (!controllers[scope]) {
        controllers[scope] = [];
      }
      const controller = new AbortController();
      controllers[scope].push(controller);
      return controller.signal ;
    },
    clear(scope) {
      if (controllers[scope]) {
        controllers[scope].forEach(controller => controller.abort());
        delete controllers[scope];
      }
    },
    //20240531 sw : ctrller도 같이 관리 
    addCtrl(scope) {
      if (!controllers[scope]) {
        controllers[scope] = [];
      }
      const controller = new AbortController();
      controllers[scope].push(controller);
      //return controller.signal ;
      return { signal: controller.signal, controller };
    },
    clearCtrl(scope, controller) {
      if (controllers[scope]) {
        controllers[scope] = controllers[scope].filter(ctrl => ctrl !== controller);
        if (controllers[scope].length === 0) {
          delete controllers[scope];
        }
      }
    },
    //모든 ctrller 제거
    clearAll() {
      Object.keys(controllers).forEach(scope => {
        this.clear(scope);
      });
    }


  };
};

export default abortController;