import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import networkManager from "@/network/networkManager"
import './plugins/link'


// Vue 앱 생성
const app = createApp(App)

// 전역 변수 선언
app.config.globalProperties.$netMngr = networkManager


// 플러그인 사용
app.use(router)

// 앱 마운트
app.mount('#app')