

const getNav = (layout) => {
return  [{
      path: '/',
      name: 'home',
      component: layout,
      children: [
        {
          path: '',
          name: 'main',
          component: () => import('@/views/page/Main.vue'), // 해당 페이지 방문 시 vue 파일을 로드함.
          props: true
        }
      ]
    },

  ]
}


export { getNav }