// import Vue from 'vue'
// import VueRouter from 'vue-router' 
// Vue.use(VueRouter)
import { createRouter, createWebHistory } from 'vue-router'
import layout from '@/views/layout/default/Index.vue'
//import load from './pub/loadType'

import { getNav } from './pub/nav.js'

const routes = [
  ...getNav(layout)
]

// 라우터 인스턴스 생성
const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
})

export default router
